.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

a {
  background:#79B473;
  color:#ffffff;
  font-size:18px;
  padding:8px 12px;
  border-radius:25px;
  text-decoration:none;
  
  display: inline-block; /* Added */
}
button{
  background:#79B473;
  color:#ffffff;
  font-size:20px;
  font-weight:200px;
  padding:16px 24px;
  width: 25%;
  border-radius:25px;
  text-decoration:none;
  border-style:none;
  font-family: 'Oxygen', sans-serif;
  display: inline-block; /* Added */
}
div {
  margin: 10px;
}
h1{
  font-size:48px;
  padding-top: 15px;
  text-align: center;
  height: 25%;
  font-family: 'Oxygen', sans-serif;
}
.color{
  padding-top: 15px;
  height: 60px;
  width: 70%;
  margin-left: auto;
  text-align: center;
  margin-right: auto;  
}
h2{
  font-size:18px;
  padding-top: 20px 15px;
  text-align: center;
  background-color: #ffffff;
  font-family: 'Oxygen', sans-serif;
}
.center {
  margin-left: auto;
  margin-right: auto;  
  text-align: center;
}

/* If the text has multiple lines, add the following: */
.center p {
  line-height: 1.2;
  display: inline-block;
  vertical-align: middle;
}
p{
  padding: 0% 20%;
  font-size:24px;
}
.Box {
  width: 20em;
  height: 20em;
}
.dot {
  height: 60px;
  width: 60px;
  background-color: #ffffff;
  display: inline-block;
}
table{
  margin: 0% 20%;
  float:auto;
  margin-left: auto;
  margin-right: auto;  
}
table td{
  padding: 12px 12px 0px 0px;
}
#log-in{
  font-size:20px;
  padding:16px 24px;
  width: 25%;
  border-radius:25px;
  font-family: 'Oxygen', sans-serif;
}